<template>
  <div class="frame-container" style="background: #eee;">
    <div class="vm-padding-height page-home-head">
      <div class="vm-postion-box">
        <div class="flex tradeTitle">
          <p @click="goback">
            <i style="font-size:16px;" class="el-icon-arrow-left"></i>
          </p>
          <p style="font-weight: 600;">商家销售额</p>
          <p></p>
        </div>
        <div class="label flex" style="padding:10px;background: #fff;margin-top:3px;">
          <div style="display:flex;justify-content: flex-start; align-items: center;width:80%">
            <el-date-picker
              style="width:45%"
              @change="getstartTime"
              size="mini"
              v-model="startTime"
              value-format="yyyy-MM-dd HH:mm:ss"
              type="date"
              placeholder="开始时间"
            ></el-date-picker>
            <span>-</span>
            <el-date-picker
              @change="getendTime"
              style="width:45%"
              size="mini"
              value-format="yyyy-MM-dd HH:mm:ss"
              v-model="endTime"
              type="date"
              placeholder="结束时间"
            ></el-date-picker>
          </div>
          <p>
            销售总额
            <i @click="get_isorder" :class="isorder?'el-icon-caret-bottom':'el-icon-caret-top'"></i>
          </p>
        </div>
        <div class="dataOverview">
          <div class="home-container-echart" style="height:100%;padding-bottom:10px">
            <!--  -->
            <ul
              class="rankingUrl"
              style="display: -webkit-box;-webkit-box-orient: vertical;-webkit-line-clamp: 3;overflow: hidden;"
            >
              <li v-for="(item,index) in rankList" :key="index">
                <p class="rank_label">{{item.storeName }}</p>
                <div class="flex">
                  <p>
                    <span v-if="item.companyType===1">#我的</span>
                    <span v-if="item.companyType===2">#一级代理</span>
                    <span v-if="item.companyType===3">#二级代理</span>
                    <span v-if="item.companyType===4">#三级代理</span>
                    <span>业务员：{{item.companyName }}</span>
                  </p>
                  <p class="money">￥{{item.countPayPrice }}</p>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { homeApi } from "@/api";
import * as types from "@/router/types";
import { afterLoginInfoLocal } from "@/storage";

export default {
  data() {
    return {
      isActive: 1,
      companyId: "",
      userType: "",
      levelAlias: {},
      startTime: "",
      endTime: "",
      dataList: "",
      dealList: "",
      type: 1,
      mapList: "",
      payList: [],
      rankList: [],
      payPriceType: 0,
      isorder: true
    };
  },
  created() {
    this.startTime = this.getDay(-30);
    this.endTime = this.getDay(0);
    let userInfo = afterLoginInfoLocal.getJSON();
    this.levelAlias = userInfo.levelAlias;
    this.companyId = userInfo.companyId;
    this.userType = userInfo.userType;
    this.getranking();
  },
  methods: {
    get_isorder() {
      this.isorder = !this.isorder;
      if(this.isorder){
        this.payPriceType = 0;
      }else{
        this.payPriceType = 1;
      }
      this.getranking();
    },
    getstartTime(val) {
      console.log(val, "val");
      this.startTime = val;
      this.getranking();
    },
    getendTime(val) {
      console.log(val, "val");
      this.endTime = val;
      this.getranking();
    },
    cheackAll() {
      this.$router.push({ name: types.SALEROOM });
    },
    goback() {
      this.$router.back(-1);
    },
    //获取排行榜
    getranking() {
      homeApi
        .ranking(
          this.companyId,
          this.startTime,
          this.endTime,
          this.payPriceType
        )
        .then(
          res => {
            this.rankList = res.obj;
          },
          err => {
            this.$toast.error(err.msg);
          }
        );
    },
    // 获取日期
    getDay(day) {
      var today = new Date();
      var targetday_milliseconds = today.getTime() + 1000 * 60 * 60 * 24 * day;
      today.setTime(targetday_milliseconds); //注意，这行是关键代码
      var tYear = today.getFullYear();
      var tMonth = today.getMonth();
      var tDate = today.getDate();
      var tHours = today.getHours();
      var tMinutes = today.getMinutes();
      var tSeconds = today.getSeconds();
      tMonth = this.doHandleMonth(tMonth + 1);
      tDate = this.doHandleMonth(tDate);
      return (
        tYear +
        "-" +
        tMonth +
        "-" +
        tDate +
        " " +
        tHours +
        ":" +
        tMinutes +
        ":" +
        tSeconds
      );
    },
    doHandleMonth(month) {
      var m = month;
      if (month.toString().length == 1) {
        m = "0" + month;
      }
      return m;
    }
  }
};
</script>
<style scoped>
.tradeTitle {
  padding: 10px;
  background: #fff;
  height: 40px;
}
</style>



